.footer {
  padding: 1rem 1.5rem;
  font-family: Heebo, sans-serif;
  background-image: linear-gradient(90deg, rgba(0,95,115,1) 0%, rgba(10,147,150,1) 69%, rgba(0,95,115,1) 100%);

  .content-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;

    @media screen and (max-width: 640px) {
      flex-direction: column;
      gap: 2rem;
    }

    .social-links {
      display: flex;
      align-items: center;
      gap: 1rem;
    }
  }

  @media screen and (max-width: 640px) {
    font-size: 0.8rem;
  }
}