.app-header {
  top: 0;
  background-image: linear-gradient(90deg, rgba(0,95,115,1) 0%, rgba(10,147,150,1) 69%, rgba(0,95,115,1) 100%);
  font-family: Heebo, sans-serif;
  z-index: 10;

  .nav-link {
    font-size: 1.1rem;
    text-transform: uppercase;
    border: 1px solid var(--hunlight);
    margin-right: 2rem;
    color: var(--hunlight);

    &:hover {
      background-image: none;
      background-color: var(--hunwarning);
      color: var(--hundark);
    }

    &.active {
      color: var(--hunlight);
    }
  }

  .collapsing,
  .collapse.show {
    border-top: 1px solid var(--hunlight);
    border-bottom: 1px solid var(--hunlight);
    padding: 1rem 0;
    .navbar-nav {
      border-bottom: 1px solid var(--hunlight);
      .nav-link {
        border: none;
        margin-right: 0;
        text-align: center;
      }
    }
    .btn-wrapper {
      display: flex;
      justify-content: center;
      margin-top: 1rem;
    }
  }
}
