.hun-accordion {
  font-family: Heebo, sans-serif;
  .accordion {
    border: 2px solid var(--hunlight);
    background-color: var(--hunwarning);

    .accordion-item {
      .title {
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 1.6rem;
        padding: 1rem;
        color: var(--hundark);
        text-transform: uppercase;
        background-color: transparent;
        border-bottom: 1px solid var(--hunlight);
        background-color: var(--hunwarning);
        font-family: Heebo, sans-serif;

        @media screen and (max-width: 640px) {
          font-size: 1.4rem;
        }

        &::after {
          font-size: 1rem;
        }
      }

      .panel {
        // border-bottom: 1px solid white;
        background-color: var(--hunwarning);

        .content {
          padding: 1rem;
          font-style: italic;
          font-size: 1.3rem;
          color: var(--hundark);
          li, p {
            color: var(--hundark);
          }
          a {
            color: var(--primary);
          }
          @media screen and (max-width: 640px) {
            font-size: 1rem;
            padding-bottom: 1.5rem;
          }
        }
      }

      
      &:last-child {
        .title {
          border-radius: 0;
        }
      }
    }
  }
}