.round-btn {
  padding: 0.5rem 2rem;
  border: 1px solid white;
  border-radius: 99px;
  font-family: Heebo, sans-serif;
  font-size: 1rem;
  text-transform: uppercase;
  &.danger {
    background-color: var(--hundanger);
    &:hover {
      background-color: var(--hunred);
    }
  }
  &.primary {
    background-color: var(--primary);
    &:hover {
      background-color: var(--secondary);
    }
  }
  &.a {
    color: var(--hunlight);
    background-color: var(--primary);
    &:hover {
      background-color: var(--secondary);
    }
  }
  &.transparent {
    background-color: transparent;
  }
  &:disabled {
    background-color: var(--hundisabled);
    opacity: 1;
  }
}