.home-container {
  min-height: 100%;
  
  .hero-container {
    padding-top: 5rem;
    padding-bottom: 3rem;
    background-image: linear-gradient(90deg, rgba(0,95,115,1) 0%, rgba(10,147,150,1) 69%, rgba(0,95,115,1) 100%);

    @media screen and (max-width: 640px) {
      padding-top: 2.5rem;
    }

    p.contact {
      font-size: 0.8rem;
      margin-bottom: 2rem;
    }
    
    .social-links {
      display: flex;
      align-items: center;
      gap: 1rem;
      margin-bottom: 2rem;
    }

    .btn-group {
      display: flex;
      gap: 2rem;
      margin-bottom: 2rem;
    }

    .number-of-holders {
      display: flex;
      align-items: center;
      gap: 1rem;
      .number {
        font-size: 4rem;
        color: var(--hunyellow);
      }
      .desc {
        font-size: 1.2rem;
        width: 8rem;
      }
    }

    .hero-img-container {
      width: 100%;
      padding: 2rem;
      border: 1px solid var(--hunlight);
    }
  }

  .about-container {
    padding-top: 3rem;
    padding-bottom: 3rem;
    background-color: var(--hunwarning);
    .section-title,
    .description {
      color: var(--hundark);
      margin-bottom: 3rem;
    }

    .description {
      line-height: 1.6rem;
    }
  }

  .join-container {
    padding-top: 3rem;
    padding-bottom: 3rem;
    background-image: linear-gradient(90deg, rgba(0,95,115,1) 0%, rgba(10,147,150,1) 69%, rgba(0,95,115,1) 100%);
    .section-title {
      margin-bottom: 3rem;
    }

    .buy-hun-img-container,
    .buy-hun-video-container {
      padding: 2rem;
      border: 1px solid var(--hunlight);
      width: 60%;
      min-width: 400px;
      max-width: 600px;
      margin: auto;
      margin-bottom: 1rem;
    }
  }

  .roadmap-container {
    padding-top: 3rem;
    padding-bottom: 3rem;
    background-color: var(--hunwarning);
    .section-title,
    .description {
      color: var(--hundark);
    }
    .section-title {
      margin-bottom: 3rem;
    }
    .description {
      line-height: 1.6rem;
      text-align: center;
      margin-bottom: 2rem;
    }
    .roadmap-swiper {
      .swiper-slide {
        padding: 0 0 2.5rem 2rem;
        .slider-content-wrapper {
          display: flex;
          flex-direction: row;
          align-items: center;
          gap: 2rem;

          @media screen and (max-width: 640px) {
            flex-direction: column;
            justify-content: center;
          }
          .img-container {
            width: 40%;
            max-width: 450px;
            min-width: 220px;
          }
          .desc {
            h5 {
              color: var(--hundanger);
              font-size: 2rem;
            }
            ul {
              list-style: none;
              padding: 0;
              li {
                color: var(--hundark);
              }
            }
          }
        }
      }
      .swiper-pagination-bullet {
        width: 20px;
        height: 20px;
        text-align: center;
        line-height: 20px;
        font-size: 12px;
        color: #000;
        opacity: 1;
        background: rgba(0, 0, 0, 0.2);
      }
      
      .swiper-pagination-bullet-active {
        color: #fff;
        background: var(--primary);
      }
    }
  }

  .team-container {
    background-image: linear-gradient(90deg, rgba(0,95,115,1) 0%, rgba(10,147,150,1) 69%, rgba(0,95,115,1) 100%);
    padding-top: 3rem;
    padding-bottom: 3rem;
    .section-title {
      margin-bottom: 3rem;
    }

    .member-card {
      border: 1px solid var(--hunlight);
      padding: 2rem;
      position: relative;
      margin-bottom: 1rem;
      .info-container {
        position: absolute;
        width: 80%;
        left: 10%;
        bottom: 2.8rem;
        text-align: center;
        p {
          background-color: white;
          margin: 0;
          padding-top: 0.5rem;
          padding-bottom: 0.5rem;
        }
        p.position {
          background-color: var(--hundark);
        }
        p.name {
          background-color: var(--hunwarning);
          color: var(--hundark);
          text-transform: uppercase;
        }
      }
    }
  }

  .faq-container {
    padding-top: 3rem;
    padding-bottom: 3rem;
    background-color: var(--hunwarning);
    .section-title {
      color: var(--hundark);
      margin-bottom: 3rem;
    }
    .description {
      color: var(--hundark);
      text-align: center;
      line-height: 1.6rem;
      margin-bottom: 3rem;
    }
  }
}